<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <CommonMissStationHeader></CommonMissStationHeader>
      </div>

      <el-row :gutter="20">
        <el-col :span="14">
          <div>
            <span>应加工物料数量 : {{ form.response_datas.length }}</span>
            <span class="ml20">
              已加工物料数量 : {{ form.matter_arr.length }}/
              {{ form.response_datas.length }}</span
            >
            <span class="ml20" v-if="QualifiedFlag">
              产品编码 : {{ form.finished_product_code }}</span
            >
          </div>

          <el-form
            class=""
            style="padding: 0 60px 0 20px; width: 90%; margin: 20px 0 40px"
            ref="formRef"
            :model="form"
            :rules="formRules"
            label-width="none"
          >
            <el-form-item
              prop="finished_product_code"
              label="产品编码"
              :rules="[{ required: true, message: '产品编码不能为空' }]"
              v-show="!QualifiedFlag"
            >
              <!-- v-if="!pack_idFlag" -->
              <el-input
                ref="firstInput"
                v-model="form.finished_product_code"
                @input="inputChange"
                clearable
              ></el-input>
            </el-form-item>

            <div
              v-for="(item, index) in form.response_datas"
              :key="index + ' onlyItem'"
            >
              <!-- :rules="[
              {
                required: true,
                message: '物料编码不能为空',
                trigger: 'blur',
              },
              {
                pattern: item.rule,
                message: '编码需要包含' + item.rule,
                trigger: 'blur',
              },
              {
                min: item.code_length,
                max: item.code_length,
                message: '编码长度为' + item.code_length + '个字节。',
                trigger: 'blur',
              },
            ]"
            :label="item.matter_name + '编码' + (index + 1) + '.'"
          > -->
              <el-form-item
                v-show="form.matter_arr.length == index && QualifiedFlag"
                :prop="'response_datas.' + index + '.matter_id'"
                :label="item.matter_name + '编码' + (index + 1) + ':'"
                label-width="none"
              >
                <el-input
                  :ref="'matterInput' + index"
                  v-model="item.matter_id"
                  @input="
                    nextFocus(
                      'matterInput' + (index + 1),
                      index,
                      item.matter_code,
                      $event
                    )
                  "
                ></el-input>
              </el-form-item>
            </div>

            <el-form-item
              label="检验结果"
              prop="test_result"
              v-show="test_resultFlag"
            >
              <el-select
                v-model="form.test_result"
                placeholder="请选择"
                style="width: 100%"
              >
                <el-option label="合格" value="合格"></el-option>
                <el-option label="不合格" value="不合格"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              label="不合格原因"
              prop="description"
              v-if="form.test_result == '不合格'"
            >
              <el-select
                v-model="form.description"
                placeholder="请选择"
                style="width: 100%"
              >
                <el-option
                  v-for="(item, index) in UnqualifiedResultCodeNameList"
                  :key="index + ' dialog'"
                  :label="
                    item.unqualified_result_code +
                    '_' +
                    item.unqualified_result_name
                  "
                  :value="item.unqualified_result_code"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-form>

          <div>
            <el-button @click="resetForm">重 置</el-button>
            <el-button
              style="margin-left: 60px"
              type="primary"
              @click="dialogSave"
              >确 定</el-button
            >
          </div>
        </el-col>

        <el-col :span="10">
          <div ref="PieChart" class="chart chartClass"></div>
        </el-col>
      </el-row>
    </el-card>
    <!-- 帅选条件 -->
    <div class="formDiv mt20">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
        <el-form-item label="时间">
          <el-date-picker
            v-model="listQuery.daterangeArr"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            format="yyyy-MM-dd"
            @change="daterange_change"
          >
          </el-date-picker>
        </el-form-item>

        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查 询</el-button
          >
          <el-button size="medium" @click="resetTable()">重 置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 表格 -->
    <el-card class="box-card mt20">
      <div slot="header" class="clearfix">
        <span>过站信息</span>
      </div>
      <div class="">
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="true"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";
import CommonMissStationHeader from "@/components/CommonMissStationHeader";
import ELTable from "@/components/formTablePage/ElTable";
import { formatDate } from "@/assets/libs/unit";

import * as echarts from "echarts";

export default {
  name: "MissStation",
  components: {
    CommonMissStationHeader,
    ELTable,
  },
  created() {
    window.addEventListener("keydown", this.handleKeyDown, true); //开启监听键盘按下事件
  },
  data() {
    return {
      myChart: null,
      seriesData: [],
      titleTotal: 0,
      timeout: "",
      Package_Qty: "",
      Shipping_SN_length: "",
      Product_length: "",
      QualifiedFlag: false,
      test_resultFlag: false,
      // 产品过站弹出层
      form: {
        product_plan_code: "",
        finished_product_code: "",
        matter_arr: [],
        response_datas: [],
        test_result: "合格",
        description: "",
      },
      formBase: {
        product_plan_code: "",
        finished_product_code: "",
        matter_arr: [],
        response_datas: [],
        test_result: "合格",
        description: "",
      },
      formRules: {
        test_result: [
          { required: true, message: "请选择检验结果", trigger: "change" },
        ],
      },
      response_datasRules: {
        matter_id: [
          {
            required: true,
            message: "数量不能为空",
            trigger: "blur",
          },
        ],
      },

      // 筛选
      listQuery: {
        daterangeArr: [
          formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
          formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
        ],
        start_time: formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
        end_time: formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
        user_code: "",
        work_code: "",
        test_result: "",
        product_plan_code: this.ProductionPlanCode,
        // product_plan_code: "Im_ProductPlan_1",
        product_code: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        daterangeArr: [
          formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
          formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
        ],
        start_time: formatDate(new Date(), "yyyy-MM-dd 00:00:00"),
        end_time: formatDate(new Date(), "yyyy-MM-dd 23:59:59"),
        matter_code: "",
        user_code: "",
        work_code: "",
        test_result: "",
        product_plan_code: this.ProductionPlanCode,
        // product_plan_code: "Im_ProductPlan_1",
        product_code: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 表格列
      tableCol: [
        {
          label: "生产计划",
          field: "product_plan_name",
          align: "center",
        },
        {
          label: "产品名称",
          field: "product_name",
          align: "center",
        },
        {
          label: "产品编码",
          field: "finished_product_code",
          align: "center",
        },

        {
          label: "工站名称",
          field: "work_name",
          align: "center",
        },
        {
          label: "操作人员",
          field: "user_name",
          width:"200px",
          align: "center",
        },

        {
          label: "检验结果",
          width:"200px",
          field: "test_result",
          align: "center",
          colType: "tag",
          tagRender: (row) => {
            if (row.test_result == "PASS") {
              row.test_result = "合格";
            } else if (row.test_result == "FAIL") {
              row.test_result = "不合格";
            }
            let params = {
              tagType: "",
              tagText: row.test_result,
            };
            if (row.test_result == "合格") {
              params.tagType = "";
            } else {
              params.tagType = "warning";
            }
            return params;
          },
        },
        {
          label: "时间",
          field: "out_time",
          align: "center",
        },

        // {
        //   label: "操作",
        //   colType: "solt",
        //   align: "center",
        //   option: [
        //     {
        //       label: "详情",
        //       event: this.colDetailBtn,
        //     },
        //   ],
        // },
      ],
      tableData: [],
    };
  },
  computed: {
    ...mapState([
      "ProductionPlanCode",
      "ProductPlanCodeNameList",
      "MatterCodeNameList",
      "BomMatterCodeNameList",
      "PersonCodeNameList",
      "CurrentUserInfo",
      "WorkCodeNameList",
      "UnqualifiedResultCodeNameList",
    ]),
  },
  methods: {
    ...mapActions([
      "getLogisticProductPlanDeal",
      "getLogisticProductPlanCodeName",
      "getLogisticMatterNameCode",
      "getLogisticBomMatterCodeName",
      "getLogisticPersonCodeName",
      "getLogisticWorkCodeName",
      "getLogisticNopageWorkUnqualifiedResultName",
    ]),
    daterange_change() {
      if (this.listQuery.daterangeArr.length > 0) {
        this.listQuery.start_time =
          this.listQuery.daterangeArr[0] + " 00:00:00";
        this.listQuery.end_time = this.listQuery.daterangeArr[1] + " 23:59:59";
      }
    },

    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticTransitProductQualifiedSearch(this.listQuery);
    },
    // 表格重置
    resetTable() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));

      this.getLogisticTransitProductQualifiedSearch(this.listQuery);
    },
    // 打印参数列表 查询
    getLogisticOperationSystem() {
      baseManagement
        .getLogisticOperationSystem({
          page: 1,
          page_size: 10,
        })
        .then((res) => {
          console.log("打印参数列表 查询", res);
          if (res.code == 0) {
            this.Package_Qty = res.data.data[0].Package_Qty;
            this.Shipping_SN_length = res.data.data[0].Shipping_SN_length;
            this.Product_length = res.data.data[0].Product_length;
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },

    inputChange(value) {
      let inputVal = value;

      if (this.timeout) {
        clearTimeout(this.timeout);
      }
      this.timeout = setTimeout(() => {
        if (this.form.finished_product_code.length != this.Product_length) {
          this.$message.warning(
            "产品编码长度为" + this.Product_length + "个字节。"
          );
          return false;
        }
        if (this.form.finished_product_code.length == 0) {
          return false;
        }

        if (this.form.finished_product_code == inputVal) {
          if (this.form.finished_product_code) {
            let params = {
              finished_product_code: this.form.finished_product_code,
            };
            baseManagement
              .getLogisticQualifiedMatterCode(params)
              .then((res) => {
                console.log("寻找此工站的上一站的物料是否合格接口： 查询", res);
                if (res.code == 0) {
                  this.QualifiedFlag = true;
                  this.$nextTick(() => {
                    this.$refs.matterInput0[0].focus();
                  });
                } else {
                  this.$message({
                    message: res.message,
                    type: "error",
                  });
                }
              });
          }
        }
      }, 500);
    },

    nextFocus(nextRef, index, matter_code) {
      if (this.timeout) {
        clearTimeout(this.timeout);
      }

      this.timeout = setTimeout(() => {
        if (this.form.response_datas[index].matter_id == "") {
          this.$message.warning("物料编码不能为空");
          return false;
        }
        if (
          this.form.response_datas[index].matter_id.indexOf(
            this.form.response_datas[index].rule
          ) < 0
        ) {
          this.$message.warning(
            "物料编码需要包含" + this.form.response_datas[index].rule
          );
          return false;
        }
        if (
          this.form.response_datas[index].matter_id.length !=
          this.form.response_datas[index].code_length
        ) {
          this.$message.warning(
            "物料编码长度为" +
              this.form.response_datas[index].code_length +
              "个字节。"
          );
          return false;
        }

        this.form.matter_arr.push(this.form.response_datas[index].matter_id);

        if (index < this.form.response_datas.length - 1) {
          this.$nextTick(() => {
            this.$refs[nextRef][0].focus();
          });
        } else {
          this.test_resultFlag = true;
        }
      }, 500);
    },

    // 通过工站获取BOM产品物料：
    async getLogisticWorkStationGetData() {
      // 产品　物料code和名称接口 查询
      await this.getLogisticBomMatterCodeName();
      baseManagement.getLogisticWorkStationGetData().then((res) => {
        console.log("通过工站获取BOM产品物料： 查询", res);
        console.log(
          "产品　物料code和名称接口 查询 BomMatterCodeNameList",
          this.BomMatterCodeNameList
        );
        if (res.code == 0) {
          let resData = res.data[0].response_datas;
          let newResData = [];

          resData.forEach((resDataItem, index) => {
            this.BomMatterCodeNameList.forEach((BomMatterCodeNameItem) => {
              if (
                resDataItem.matter_code == BomMatterCodeNameItem.matter_code
              ) {
                resDataItem.matter_name = BomMatterCodeNameItem.matter_name;
                resDataItem.rule = BomMatterCodeNameItem.rule;
                resDataItem.code_length = BomMatterCodeNameItem.code_length;
              }
            });
          });
          for (let index = 0; index < resData.length; index++) {
            for (
              let itemIndex = 0;
              itemIndex < resData[index].install_number;
              itemIndex++
            ) {
              newResData.push({
                matter_name: resData[index].matter_name,
                matter_code: resData[index].matter_code,
                matter_id: resData[index].matter_id,
                rule: resData[index].rule,
                code_length: resData[index].code_length,
              });
            }
          }
          this.form.response_datas = newResData;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 产品过站 新增
    postLogisticProductTransitInfo(params) {
      baseManagement.postLogisticProductTransitInfo(params).then((res) => {
        console.log("产品过站 新增", res);
        if (res.code == 0) {
          this.resetForm();

          // 过站查询 查询
          this.getLogisticTransitProductQualifiedSearch(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 重置按钮
    resetForm() {
      this.form = JSON.parse(JSON.stringify(this.formBase));
      this.getLogisticWorkStationGetData();
      this.QualifiedFlag = false;
      this.test_resultFlag = false;
      this.form.test_result == "合格";
      this.$nextTick(() => {
        this.$refs.formRef.clearValidate();
        this.$refs.firstInput.focus();
      });
    },

    // 确认按钮
    dialogSave() {
      if (!this.test_resultFlag) {
        return false;
      }
      if (!this.ProductionPlanCode) {
        this.$message.warning("没有进行中的生产计划！");
        return false;
      }
      if (this.form.finished_product_code.length != this.Product_length) {
          this.$message.warning(
            "产品编码长度为" + this.Product_length + "个字节。"
          );
          return false;
        }
      if (this.form.matter_arr.length < this.form.response_datas.length) {
        this.$message.warning("存在未加工物料！");
        return false;
      }
      this.$refs.formRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          console.log("产品过站", this.form);
          this.form.product_plan_code = this.ProductionPlanCode;

          let params = JSON.parse(JSON.stringify(this.form));

          if (this.QualifiedFlag) {
            if (params.test_result == "合格") {
              params.test_result = "PASS";
            } else if (params.test_result == "不合格") {
              params.test_result = "FAIL";
            }
            this.postLogisticProductTransitInfo(params);
          } else {
            this.$message({
              message: "该产品上一工站不合格",
              type: "error",
            });
          }
        }
      });
    },

    handleKeyDown(e) {
      let key = null;
      if (window.event === undefined) {
        key = e.keyCode;
      } else {
        key = window.event.keyCode;
      }
      if (key === 13) {
        this.dialogSave();
      }
    },
    // 过站查询 查询
    getLogisticTransitProductQualifiedSearch(params) {
      params.product_plan_code = this.ProductionPlanCode;
      baseManagement
        .getLogisticTransitProductQualifiedSearch(params)
        .then((res) => {
          console.log("过站查询 查询", res);
          if (res.code == 0) {
            let tableData = res.data.data;
            this.seriesData = [
              { value: res.data.pass_num, name: "合格" },
              { value: res.data.fail_num, name: "不合格" },
            ];
            this.titleTotal = res.data.pass_num + res.data.fail_num;
            this.chartInit();
            tableData.forEach((tableDataItem) => {
              this.ProductPlanCodeNameList.forEach((CodeNameListItem) => {
                if (
                  tableDataItem.product_plan_code ==
                  CodeNameListItem.product_plan_code
                ) {
                  tableDataItem.product_plan_name =
                    CodeNameListItem.product_plan_name;
                }
              });

              this.PersonCodeNameList.forEach((CodeNameListItem) => {
                if (tableDataItem.user_code == CodeNameListItem.user_code) {
                  tableDataItem.user_name = CodeNameListItem.user_name;
                }
              });
              this.MatterCodeNameList.forEach((CodeNameListItem) => {
                if (tableDataItem.matter_code == CodeNameListItem.matter_code) {
                  tableDataItem.matter_name = CodeNameListItem.matter_name;
                }
              });
              this.WorkCodeNameList.forEach((CodeNameListItem) => {
                if (tableDataItem.work_code == CodeNameListItem.work_code) {
                  tableDataItem.work_name = CodeNameListItem.work_name;
                }
              });
            });

            this.tableData = tableData;
            this.listQuery.total = res.data.total;
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticTransitProductQualifiedSearch(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticTransitProductQualifiedSearch(this.listQuery);
    },

    chartInit() {
      let _this = this;
      if (
        this.myChart != null &&
        this.myChart != "" &&
        this.myChart != undefined
      ) {
        this.myChart.dispose();
      }
      this.myChart = echarts.init(this.$refs.PieChart);

      // this.myChart.showLoading();

      //   myChart.showLoading();
      let option = {};
      // this.noneFlag = true;
      this.noneFlag = false;
      for (let i in this.seriesData) {
        if (this.seriesData[i].value > 0) {
          this.noneFlag = true;
        }
      }

      if (this.noneFlag) {
        option = {
          title: {
            text: "总计:" + this.titleTotal,
            top: "5%",
            right: "8%",
            textStyle: {
              // color: "#fff",
              fontSize: "16",
            },
          },
          color: ["#5470c6", "#FAC858"],
          tooltip: {
            trigger: "item",
            formatter: (params) => {
              return (
                params.name +
                ":  " +
                params.value +
                " " +
                ((params.value / this.titleTotal) * 100).toFixed(2) +
                "%"
              );
            },
          },
          legend: {
            type: "scroll",
            orient: "vertical",
            top: "center",
            right: "3%",
            textStyle: {
              padding: 5,
            },

            // formatter: function (params) {
            //   console.log(12321321, params);
            //   let value = 0;
            //   for (let i in _this.seriesData) {
            //     if (params == _this.seriesData[i].name) {
            //       value = _this.seriesData[i].value;
            //     }
            //   }
            //   return `${params} - ${value}`;
            // },
          },
          series: [
            {
              // name: this.seriesName,
              // name: '公司所用工时',
              type: "pie",
              radius: ["45%", "75%"],
              center: ["50%", "50%"],
              avoidLabelOverlap: false,
              label: {
                show: false,
                position: "center",
              },
              // itemStyle: {
              //   normal: {
              //     borderColor: "#fff",
              //     borderWidth: 2,
              //   },
              // },
              emphasis: {
                label: {
                  show: true,
                  // fontSize: "20",
                  // fontWeight: "bold",
                },
              },
              label: {
                // show:noneFlag,
                formatter: " {b|{b}：}{c}  {per|{d}%}  ",
                // backgroundColor: '#F6F8FC',
                // borderColor: '#8C8D8E',
                // borderWidth: 1,
                borderRadius: 4,

                rich: {
                  // b: {
                  //     color: '#4C5058',
                  //     fontSize: 14,
                  //     fontWeight: 'bold',
                  //     lineHeight: 33
                  // },
                  per: {
                    // color: '#fff',
                    // backgroundColor: '#4C5058',
                    // padding: [3, 4],
                    borderRadius: 4,
                  },
                },
              },
              labelLine: {
                // show: false,
              },
              data: this.seriesData,
            },
          ],
        };
      } else {
        //  this.myChart.hideLoading();
        option = {
          title: {
            text: "无数据",
            top: "center",
            left: "center",
          },
          series: [],
        };
      }
      // this.myChart.hideLoading();
      this.myChart.setOption(option);
      this.myChart.on("click", function (params) {
        _this.$emit("pieChartClick", params);
      });

      window.addEventListener("resize", function () {
        _this.myChart.resize();
      });
    },
 
 },
  mounted() {
    // 打印参数列表 查询
    this.getLogisticOperationSystem();
    // 进行中的生产计划接口：
    this.getLogisticProductPlanDeal();
    // 通过工站获取BOM产品物料：
    this.getLogisticWorkStationGetData();
    // 生产计划内码和名称接口 查询
    this.getLogisticProductPlanCodeName();
    // 人员内码和人员名称 查询
    this.getLogisticPersonCodeName();
    // 物料内码和物料名称 查询
    this.getLogisticMatterNameCode();
    // 工站内码和工站名称接口： 查询
    this.getLogisticWorkCodeName();
    // 不良原因code和name接口 查询
    this.getLogisticNopageWorkUnqualifiedResultName({
      work_id: this.CurrentUserInfo.work_id,
    });
    // 过站查询 查询

    this.getLogisticTransitProductQualifiedSearch(this.listQuery);

    this.$nextTick(() => {
      this.$refs.firstInput.focus();
    });
  },
};
</script>

